import * as React from 'react';

import { Response } from '@miq/utiljs';
import { HView, Icons, Loading } from '@miq/componentjs';

import { StaffNavLink } from '../../components';
import StaffView from '../../Views';

import { TLIB, useLIBListRequest } from './utils';

export default function LIBListView() {
  const listCtx = HView.useView();
  const { res, loading } = useLIBListRequest({ summary: '1' });

  if (loading) return <Loading />;

  const r = new Response<TLIB>(res);
  if (!r.isSuccess) return null;

  return (
    <HView context={listCtx}>
      <StaffView
        title="Links in bio"
        actions={<HView.Toggle className="btn-primary-2">{listCtx.isEdit ? 'Done' : 'Edit'}</HView.Toggle>}
      >
        <HView.List<TLIB>
          className="d-grid gap-1"
          items={r.items}
          renderItem={({ data }) => {
            return (
              <div className="d-flex align-items-center justify-content-between border-1 p-1 mb-1 rounded">
                <div className="flex-1">
                  <div className="fw-bold">{data.name}</div>
                  <div className="text-sm text-muted">{`today: 123 | total: 2000`}</div>
                </div>

                <StaffNavLink to={`${data.slug}/`} className="btn btn-square btn-primary-3">
                  <Icons.Edit />
                </StaffNavLink>
              </div>
            );
          }}
        />
      </StaffView>
    </HView>
  );
}
